export default {
  name: '',
  props: {
    guan: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      str: ''
    }
  },

  methods: {},
  watch: {
    str() {
      this.$emit('update:guan', this.str)
    }
  }
}



